// 
const pathPre = '/course'
//  
export default {
    path: pathPre,
    component: resolve => {
        require.ensure([], () => {
            resolve(require('../views/course/Main.vue'))
        })
    },
    children: [{
            path: 'category',// -manage/course-category-manage
            name: 'course-index',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/course/category/Index.vue'))
                })
            },
            meta: {
                title: '',
                noback: true
            }
        },
        {
            path: 'cate-add',  //
            name: 'cate-add',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/course/category/Form.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        {
            path: 'cate-edit/:course_id',  //
            name: 'cate-edit',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/course/category/Form.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        {
            path: 'video', // 
            name: 'course-video',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/course/manage/Video.vue'))
                })
            },
            meta: {
                title: ' - ',
                noback: true
            }
        },
        {
            path: 'video/add',//
            name: 'course-video-add',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/course/video/Add.vue'))
                })
            },
            meta: {
                title: ' - ',
            }
        },
        {
            path: 'manage/public', // 
            name: 'course-manage-public',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/course/manage/Public.vue'))
                })
            },
            meta: {
                title: ' - ',
                noback: true
            }
        },
        {
            path: 'manage/add-course', // 
            name: 'course-manage-addCourse',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/course/manage/AddCourse.vue'))
                })
            },
            meta: {
                title: '-',
            }
        },
        {
            path: 'manage/course-category-manage', // 
            name: 'course-manage-course-category-manage',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/course/manage/CategoryManage.vue'))
                })
            },
            meta: {
                title: '-',
            }
        },
        {
            path: 'manage/course-answer-analysis/:id', // 
            name: 'course-manage-course-answer-analysis',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/course/manage/AnswerAnalysis.vue'))
                })
            },
            meta: {
                title: '',
            }
        },
      
    ]
}


